<template>
    <b-navbar variant="light">
      <b-navbar-brand href="#">{{ guildName }}</b-navbar-brand>
      <b-navbar-nav>
        <b-nav-item @click="changeLanguage('pt')">Português</b-nav-item>
        <b-nav-item @click="changeLanguage('en')">English</b-nav-item>      
        <b-nav-item @click="changeLanguage('es')">Español</b-nav-item>        
      </b-navbar-nav>      
    </b-navbar>
    <b-container>    
      <b-alert show>{{questions.length}} perguntas cadastradas.</b-alert>    
      <div class="addbtn">
        <b-button variant="info" @click="this.newModal=!this.newModal">Cadastrar nova pergunta</b-button>
      </div>
      <QuestionBlock v-for="question in questions" 
        :key=question.id
        :ques=question 
        @delete-question="deleteQuestion" 
        @update-question="updateQuestion" />
  
      <div class="addbtn" v-if="questions.length>0">
        <b-button variant="info" @click="this.newModal=!this.newModal">Cadastrar nova pergunta</b-button>
      </div>
    </b-container>
    <b-modal id="new-modal"
      v-model="newModal"
      title="Cadastrar pergunta"
      @ok="insertNewQuestion()">
      <b-container fluid>
        <b-form ref="form" autocomplete="off">
          <b-form-group 
            label="Enunciado / Pergunta"
            label-for="input-question">
              <b-form-textarea id="input-question" v-model="this.newQues.question" rows="6" required></b-form-textarea>
          </b-form-group>
  
          <b-form-group 
            label="Respostas"
            label-for="input-answer0">            
              <b-form-text>Resposta <b style="color:green">correta</b></b-form-text>  
              <b-form-input id="input-answer0" v-model="this.newQues.answers[0]" required></b-form-input>
              <br/>
              <b-form-text>Resposta <b style="color:red">incorreta</b></b-form-text>  
              <b-form-input id="input-answer1" v-model="this.newQues.answers[1]" required></b-form-input>
              <br/>
              <b-form-text>Resposta <b style="color:red">incorreta</b></b-form-text>  
              <b-form-input id="input-answer2" v-model="this.newQues.answers[2]"></b-form-input>
              <br/>
              <b-form-text>Resposta <b style="color:red">incorreta</b></b-form-text>  
              <b-form-input id="input-answer3" v-model="this.newQues.answers[3]"></b-form-input>
  
          </b-form-group>
        </b-form>
    </b-container>
    </b-modal>
  </template>
  
  <script>
  import QuestionBlock from '../components/Question';
  import http from '../http-common';
  import { useCookies } from "vue3-cookies";
  
  export default {
    name: 'QuizManager',
    components: {
      QuestionBlock
    },
    setup() {
      const { cookies } = useCookies();
      return { cookies };
    },
    data: () => ({    
      questions: [],
      lang: 'pt',
      newQues: {question: '', answers:[]},
      newModal: false,
      logged: '',
      guildName: ''
    }),
    mounted: function() {       
      let my_cookie_value = this.cookies.get("myCoookie");
      this.logged = my_cookie_value.guildId;
      this.guildName = my_cookie_value.guildName;
      this.loadQuestions();      
    },
    methods: {
      loadQuestions() {
        this.questions = [];
        http.get('/get/'+this.logged+'/' + this.lang)
        .then(res => {                
          Object.assign(this.questions, res.data);
        });      
      },
      deleteQuestion(ques) {      
        http.delete('/delete/' + ques.id)
        .then(()=> { this.loadQuestions(); });
        
      },
      updateQuestion(ques) {            
        http.put('/update/' + ques.id, ques.question)
        .then(()=> { this.loadQuestions(); });
      },
      insertNewQuestion() {      
        this.newModal = false;
        
        let q = {};
        Object.assign(q, this.newQues)      
        
        if(q.question && q.answers.length >= 2) {
          // this.questions.push(q);
          http.post('/insert/'+this.logged+'/'+this.lang, q)
          .then(()=> { this.loadQuestions(); });
        }
        else console.log('Pergunta mal formada!');
        this.newQues = {question: '', answers:[]}
      },
      changeLanguage(l) {
        this.lang = l;
        this.loadQuestions();
        console.log('Language changed to: ' + l);
      },
      logOff() {
        console.log('logging off');
        this.cookies.set("myCoookie", '');
      }
    }
  }
  </script>
  
  <style>
  .addbtn {
    margin-bottom: 12px;
  }
  </style>
  