<template>
    <b-container>         
        <b-row>
            <b-col>
                Redirecting ...
            </b-col>
        </b-row>      
    </b-container>
</template>
<script>
import http from '../http-common';
import { useCookies } from "vue3-cookies";

export default {
    name: 'LoginPage',
    setup() {
        const { cookies } = useCookies();
        return { cookies };
    },
    data() {
        return {
            guildName: ''
        }
    },
    mounted() {
        const gid = this.$route.params.gid;        
        http.get('/guildname/'+gid).then((res) => {
            console.log(this.cookies);
            this.cookies.set("myCoookie", {'guildId': gid, 'guildName': res.data.name});

            window.location.href="/manage";
        })
    }
}
</script>